import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { appUrl } from 'src/app/app-url.module';
import { IZone } from 'src/app/models/zone.model';
import { ZoneService } from 'src/app/services/zone.service';

@Component({
  selector: 'app-zone',
  templateUrl: './zone.page.html',
  styleUrls: ['./zone.page.scss'],
})
export class ZonePage implements OnInit {
  url = appUrl;
  zones: IZone[] = [];

  constructor(private zoneService: ZoneService) {}

  ngOnInit() {
    this.onFetchData();
  }

  isRefresh(evt: any) {
    setTimeout(() => {
      this.onFetchData();
      evt.target.complete();
    }, 1000);
  }

  onFetchData() {
    this.zoneService.getAllZone().then((observe) => {
      observe.pipe(map((val) => val.zone)).subscribe({
        next: (data) => {
          this.zones = data;
        },
        error: () => {},
        complete: () => {},
      });
    });
  }
}
